@import 'vars';
html {
    width: 100%;
    &.fixed-body {
        overflow-y: hidden;
    }
}

.wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
}

.content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    width: 100%;
}

.m-page {
    background-color: #FCFDFD;
    background-image: url(../img/page-bg.png);
    background-position: right 0 top 3.125em;
    background-repeat: no-repeat;
    background-size: 74em auto;
    width: calc(100vw - (100vw - 100%));
    overflow-x: hidden;
    font-family: $font1;
    font-weight: 400;
    color: $black;
    font-size: calc(100vw * (16 / 1440));
    //font-size: calc(1440px * (16 / 1440));
    //@media screen and (max-width: 1440px){
    //    font-size: calc(100vw * (16 / 1440));
    //}
    &.fixed-body,
    &.fixed-body2{
        overflow-y: hidden;
        padding-right: var(--scrollWidth);
    }
}

.container {
    width: 100%;
    margin: 0 auto;
    padding: 0 3.75em;
}

a {
    &:hover {
        color: $primary;
    }
}


@media screen and (max-width: 992px){
    .m-page{
        font-size: calc(100vw * (16 / 768));
        background-size: 100% auto;
    }

    .container{
        padding: 0 1em;
    }
}


@media screen and (max-width: 576px){
    .m-page{
        font-size: calc(100vw * (16 / 375));
    }
}


