.about-product{
  margin-top: 3.75em;
  &__top{
    padding-bottom: 1em;
    border-bottom: $border;
  }
  &__title{
    font-size: 1.5em;
    line-height: 129%;
    text-transform: uppercase;
    font-weight: 500;
    letter-spacing: .1em;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.875em));
    gap: 1.875em 3.75em;
    margin-top: 2.5em;
  }
}

.about{
  padding-bottom: 3.75em;
  &__grid{
    margin-top: 1.69em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .6875em));
    gap: .625em 1.375em;
  }
  &__nav{
    display: flex;
    margin-top: 1.69em;
  }
  &__images{
    margin: 3.69em 0 1.75em;
    @include grid100gap(1.375em);
    &:first-child{
      margin-top: 0;
    }
    &:last-child{
      margin-bottom: 0;
    }
  }
  &__img{
    width: 100%;
    display: block;
  }
  &__bottom{
    max-width: 24.5em;
    margin-top: 4.5em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__phone{
    margin-top: 1.375em;
    color: $primary;
    &:hover{
      color: $black;
      path{
        stroke: $black;
      }
    }
  }
}

.about__phone,
.header-contacts__phone{
  display: flex;
  align-items: center;
  svg{
    width: .9375em;
    margin-right: .375em;
  }
}

.contacts{
  &__grid{
    margin-top: 2.375em;
    display: grid;
    grid-template-columns: 29.5em calc(100% - 29.5em);
    color: $white;
    background-color: $black;
  }
  &__col{
    padding: 4.2em;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
  }
  .socials{
    margin-top: 2.25em;
  }
}

.map{
  height: 31.875em;
}


@media screen and (max-width: 992px){
  .about-product{
    margin-top: 3.125em;
    &__grid{
      margin-top: 1.875em;
      grid-template-columns: 100%;
    }
  }

  .about{
    padding-bottom: 2.5em;
    &__grid{
      margin-top: 1.25em;
      grid-template-columns: 100%;
    }
    &__nav{
      margin-top: 1.875em;
    }
    &__images{
      margin: 2.5em 0 1.25em;
      gap: .625em;
    }
    &__bottom{
      margin-top: 1em;
    }
    &__phone{
      margin-top: 1.25em;
    }
  }

  .contacts{
    &__grid{
      margin-top: 1.375em;
      grid-template-columns: 50% 50%;
    }
    &__col{
      padding: 2.75em 1em;
    }
  }

  .map{
    height: 20.25em;
    min-height: 100%;
  }
}


@media screen and (max-width: 576px){
  .contacts {
    &__grid {
      grid-template-columns: 100%;
    }
  }
}
