.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $black;
    opacity: .6;
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        background: #fff;
        width: 36.25em;
        max-width: calc(100% - 2em);
        position: relative;
        z-index: 3;
        padding: 6em;
        display: inline-block;
    }
    .h2{
        text-align: center;
    }
    .close-btn{
        width: 2.5em;
        @include center;
        position: absolute;
        top: 1em;
        right: 1em;
        img{
            width: 100%;
        }
    }
}




@media screen and (max-width: 992px){
    .modal{
        &__dialog {
            width: 21.5em;
            padding: 5.31em 1.25em;
        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__dialog {

        }
    }
}
