@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1.5em;
    height: 2.8125em;
    border-radius: .5em;
    font-weight: 500;
    text-transform: uppercase;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: .875em;
        letter-spacing: .1em;
    }
}

.m-btn-orange-outline{
    border: 1px solid $primary;
    color: $primary;
    &:hover{
        background-color: $primary;
        color: $white;
        path{
            fill: $white;
        }
    }
}

.m-btn-white-outline{
    border: 1px solid $white;
    color: $white;
    &:hover{
        background-color: $primary;
        border-color: $primary;
        color: $white;
    }
}

.round-btn{
    width: 9.5em;
    height: 9.5em;
    border-radius: 50%;
    svg{
        width: 5em;
    }
}


@media screen and (max-width: 992px){
    .round-btn{
        width: 6em;
        height: 6em;
        svg{
            width: 3em;
        }
    }
}
