.header-catalog-item,
.catalog-subitem{
  margin-bottom: 3.81em;
  &:last-child{
    margin-bottom: 0;
  }
}

.header-catalog-item{
  text-transform: uppercase;
  &:not(:first-child){
    .header-catalog-item__hidden{
      //display: none;
    }
  }
}

.catalog-subitem{
  &__hidden{
    display: none;
  }
}

.catalog-subitem2{
  height: 100%;
  display: block;
  position: relative;
  color: $white;
  &__content{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    text-align: center;
    padding: 5.125em 0 0;
  }
  &__img{
    @include imgCover(0);
    height: 100%;
  }
  &__title,
  &__desc{
    line-height: 86%;
    font-style: italic;
    font-family: $font2;
    font-weight: 500;
  }
  &__title{
    font-size: 4em;
    text-transform: capitalize;
  }
  &__desc{
    font-size: 2em;
    opacity: .5;
    margin-top: .25em;
    text-transform: lowercase;
  }
}


@media screen and (min-width: 993px){
  .header-catalog{
    border-bottom: $border;
    position: relative;
    &__items{
      width: 15.625em;
      border-right: $border;
      padding: 2.625em 0 3em 3.75em;
    }
  }

  .header-catalog-item,
  .catalog-subitem{
    &__show{
      cursor: pointer;
      transition: all .3s;
      &:hover{
        color: $primary;
      }
    }
  }

  .header-catalog-item{
    &._hover,
    &:hover{
      .header-catalog-item__show:after{
        opacity: 1;
      }
      .header-catalog-item__hidden{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &__hidden{
      position: absolute;
      top: 0;
      left: 15.625em;
      margin-left: -1px;
      width: calc(100% - 15.625em);
      height: 100%;
      padding: 2.625em 3.75em 0 3.5em;
      opacity: 0;
      pointer-events: none;
    }
  }

  .catalog-subitem{
    width: 16em;
    &._hover,
    &:hover{
      .catalog-subitem__show:after{
        opacity: 1;
      }
      .catalog-subitem__hidden{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &__show{
      display: flex;
      align-items: center;
      &:after{
        content: '';
        background-image: url(../img/next.svg);
        @include bgImgSize(100%);
        width: 4.9375em;
        height: .5em;
        margin-left: 2em;
        transition: all .3s;
        opacity: 0;
      }
    }
    &__hidden{
      display: block !important ;
      position: absolute;
      top: 2.625em;
      right: 0;
      width: calc(100% - 22.25em);
      height: calc(100% - 2.625em);
      padding-right: 3.5em;
      transition: all .3s;
      opacity: 0;
      pointer-events: none;
    }
  }
}



@media screen and (max-width: 992px){
  .header-catalog-item{
    margin-bottom: 1em;
    &__show{
      display: flex;
      align-items: center;
      &:after{
        content: '';
        flex-shrink: 0;
        width: .8125em;
        height: .4375em;
        background-image: url(../img/arrow-bottom.svg);
        @include bgImgSize(100%);
        margin-left: .375em;
        transition: all .3s;
      }
    }
    &__hidden{
      display: none;
      padding-left: 1em;
      padding-top: 1em;
    }
    &._open{
      .header-catalog-item__show:after{
        transform: rotate(-180deg);
      }
    }
  }

  .catalog-subitem{
    margin-bottom: 1em;
  }
}


