.text{
  @include grid100gap(1em);
  h5{
    text-transform: uppercase;
    font-weight: 500;
  }
  ol {
    counter-reset: list;
    li {
      counter-increment: list;
      &:before {
        content: counter(list) '. ';
      }
    }
  }
  ul{
    li{
      position: relative;
      padding-left: 1.5em;
      &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
      }
    }
  }
}



