.catalog{
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: 1em;
    margin-top: 2.125em;
  }
  .text{
    margin-top: 2em;
  }
  .collection__grid{
    grid-template-columns: repeat(4,25%);
    gap: 0;
    padding: 0;
    border-bottom: $border;
    margin-top: 2.31em;
  }
  .product-card{
    padding: 1.5em .9375em;
    border-top: $border;
    border-right: $border;
    &:nth-child(4n){
      border-right: none;
    }
  }
}

.catalog-card{
  display: block;
  position: relative;
  &__img{
    @include imgCover(60%);
  }
  &__title{
    pointer-events: none;
    @include centerAbsolute;
    font-size: 4em;
    font-style: italic;
    font-weight: 500;
    line-height: 86%;
    letter-spacing: -.05em;
    color: $white;
    z-index: 2;
    font-family: $font2;
    text-align: center;
    width: 100%;
  }
}

.suit-for-you{
  margin-top: 3.69em;
  padding-bottom: 1em;
  .d1{
    font-size: 4.625em;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - .75em));
    gap: 1em;
    grid-auto-flow: dense;
    margin-top: 1.81em;
  }
  .catalog-card{
    &__img{
      height: 35.0625em;
      padding: 0;
    }
    &__title{
      font-size: 2.5em;
    }
    &:first-child{
      grid-column: span 2;
    }
  }
}

.tags{
  .swiper-slide{
    width: auto;
    display: flex;
  }
  &__item{
    border-radius: 12.5625em;
    border: 1px solid $black;
    padding: 0 1.875em;
    @include center;
    white-space: nowrap;
    height: 2.8125em;
    text-transform: uppercase;
    font-weight: 500;
    margin-right: 1.625em;
    &:last-child{
      margin-right: 0;
    }
    &:hover,
    &._active{
      color: $primary;
      border-color: $primary;
    }
    span{
      font-size: .9375em;
      letter-spacing: .1em;
    }
  }
}


@media screen and (max-width: 992px){
  .catalog{
    overflow: hidden;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .6875em / 2));
      gap: .6875em;
      margin-top: 1.56em;
    }
    .text{
      margin-top: 1.31em;
    }
    &__top{
      display: block;
    }
    .product-card{
      padding: .5em;
    }
  }

  .catalog-card{
    &__title{
      font-size: 2.375em;
    }
  }

  .suit-for-you{
    padding-bottom: .625em;
    .d1{
      font-size: 2.375em;
    }
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .3125em));
      gap: .625em;
      margin-top: 1.25em;
    }
    .catalog-card {
      &__img {
        height: unset;
        padding-bottom: 53%;
      }
      &__title{
        font-size: 2.375em;
      }
      &:first-child{
        grid-column: 1/-1;
        .catalog-card__img{
          padding-bottom: 75.8%;
        }
      }
    }
  }

  .tags{
    margin-top: 1.5em;
    &__item{
      border-radius: 9em;
      padding: 0 1em;
      height: 2.01em;
      margin-right: .625em;
      span{
        font-size: 0.67306em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .catalog{
    &__grid{
      grid-template-columns: 100%;
    }
    .collection__grid{
      grid-template-columns: 50% 50%;
      margin-top: 1.25em;
    }
  }

  .suit-for-you{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
