.collection{
  &__grid{
    padding: 1.5em 1em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 5.625em / 4));
    gap: 1.875em;
  }
}

.m-section{
  &__link{
    height: 3.875em;
    border-top: $border;
    color: $primary;
    width: 100%;
    span{
      font-size: .9375em;
    }
  }
}

.main-about{
  padding: 23.75em 0 28.56em;
  min-height: 70em;
  position: relative;
  &__content{
    width: 32.3125em;
    max-width: 100%;
    margin: 0 auto;
    text-align: center;
  }
  &__title{
    font-size: 5.875em;
    line-height: 86%;
    font-family: $font2;
    font-weight: 500;
    letter-spacing: -.05em;
    font-style: italic;
  }
  &__desc{
    margin-top: 1em;
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 1.875em;
  }
  &__img{
    position: absolute;
  }
  &__img1{
    top: 4.25em;
    left: 33.5em;
    width: 20.75em;
  }
  &__img2{
    top: 18.75em;
    right: 12.5em;
    width: 10.75em;
  }
  &__img3{
    top: 30.5em;
    right: 0;
    width: 12.5em;
  }
  &__img4{
    bottom: 6.81em;
    right: 12.5em;
    width: 20.1875em;
  }
  &__img5{
    bottom: 9.94em;
    left: 25.81em;
    width: 10.75em;
  }
  &__img6{
    bottom: 18.19em;
    left: 0;
    width: 25.8125em;
  }
  &__img7{
    top: 15.56em;
    left: 2.375em;
    width: 14.4375em;
  }
}

.main{
  background-size: cover;
  background-position: center;
  color: $white;
  &:after{
    border-radius: 80.3125em;
    background: rgba(11, 38, 47, 0.25);
    filter: blur(168px);
  }
  &__inner{
    position: relative;
    z-index: 2;
    padding: 3.56em 0 3.125em;
  }
  &__owl{
    width: 13em;
    display: block;
    margin: 0 auto .81em;
  }
  &__title{
    font-size: 10.08181em;
    letter-spacing: -.05em;
    font-weight: 400;
    line-height: 86%;
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 1.625em;
  }
  &__bottom{
    margin-top: 2.31em;
  }
  &__desc{
    max-width: 20.3125em;
    font-size: 1em;
    line-height: 125%;
    font-weight: 300;
  }
}


@media screen and (max-width: 992px){
  .collection{
    &__grid{
      padding: 1em;
      grid-template-columns: repeat(4,calc(25% - 3em / 4));
      gap: 1em;
    }
  }

  .main-about{
    padding: 14.44em 0 12.75em;
    min-height: 46em;
    &__content{
      padding: 0 1em;
    }
    &__title{
      font-size: 3em;
    }
    &__images{
      font-size: .5em;
    }
  }

  .main{
    &:after{
      background: rgba(11, 38, 47, 0.50);
    }
    .container{
      padding: 0;
    }
    &__inner{
      padding: 4.3125em 0 2em;
    }
    &__owl{
      width: 11.3125em;
      display: block;
      margin-bottom: 1em;
    }
    &__title{
      text-align: center;
      font-size: 5.375em;
      line-height: 104%;
    }
    &__bottom{
      margin-top: 5.75em;
      justify-content: center;
      padding: 0 1.5em;
    }
    &__desc{
      text-align: center;
    }
    .round-btn{
      display: none;
    }
  }
}


@media screen and (max-width: 576px){
  .collection{
    &__grid{
      padding: 0;
      grid-template-columns: 50% 50%;
      gap: 0;
    }
    .product-card{
      padding: .5em;
      border-top: $border;
      &:nth-child(odd){
        border-right: $border;
      }
    }
  }

  .main-about{
    min-height: 47em;
    overflow: hidden;
    &__images{
      font-size: 1em;
    }
    &__img1{
      top: 2.56em;
      left: 2.3125em;
      width: 9.375em;
    }
    &__img2{
      top: 4.125em;
      right: 1em;
      width: 5.625em;
    }
    &__img3{
      top: 11.44em;
      right: -2em;
      width: 5.5em;
    }
    &__img4{
      bottom: 2.19em;
      right: 1em;
      width: 8.75em;
    }
    &__img5{
      display: none;
    }
    &__img6{
      bottom: 5.19em;
      left: -6.56em;
      width: 11.375em;
    }
    &__img7{
      top: 11.94em;
      left: -3.5em;
      width: 6.6875em;
    }
  }
}
