@import "../vars.scss";
.breadcrumbs {
    padding: 1.19em 0 5.25em;
    color: #6A6A6A;
    li{
        position: relative;
        display: inline;
        &:last-child {
            color: $black;
        }
        &:not(:first-child) {
            margin-left: 1.125em;
            &:before {
                content: '';
                position: absolute;
                z-index: 1;
                height: 100%;
                width: .25em;
                background-image: url(../img/bread-arrow.svg);
                @include bgImgSize(100%);
                top: .125em;
                right: 100%;
                margin-right: .5625em;
            }
        }

    }
    a,span{
        font-size: .75em;
        line-height: 167%;
        letter-spacing: .1em;
    }
}


@media screen and (max-width: 992px){
    .breadcrumbs{
        padding: 1.37em 0 2.5em;
    }
}
