.main-catalog{
  min-height: 54em;
  position: relative;
  background-size: cover;
  background-position: center;
  .product-card{
    position: absolute;
    width: 18.1875em;
    background: #FFF;
    padding: 1.25em;
    &__img{
      padding-bottom: 120%;
    }
    &:nth-child(1){
      top: 4.625em;
      left: 3.75em;
    }
    &:nth-child(2){
      right: 3.75em;
      bottom: 5em;
    }
  }
}

.product-card{
  &__img{
    @include imgCover(100%);
  }
  &__content{
    margin-top: .625em;
    display: flex;
    justify-content: space-between;
    gap: .5em;
    flex-wrap: wrap;
    color: $dark;
  }
}

.materials{
  &__inner{
    color: $white;
    position: relative;
    background-color: $black;
    &:before{
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 50%;
      height: 100%;
      background-image: url(../img/materials-bg.webp);
      background-size: cover;
    }
  }
  &__col{
    padding-top: 7.875em;
    padding-left: 3.75em;
    padding-right: 8.69em;
  }
  &__desc{
    margin-top: 1em;
  }
  &__nav{
    display: flex;
    padding-top: 1.875em;
    margin-top: auto;
  }
  &__img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
  }
}

.slider{
  &__wrap{
    overflow: hidden;
  }
  .swiper-container{
    width: calc(100% + 1em);
  }
  .swiper-wrapper{
    align-items: stretch;
  }
  .swiper-slide{
    padding-right: 1em;
    height: auto;
  }
}

.materials-slider{
  position: relative;
  z-index: 2;
  .swiper-slide{
    min-height: 36.25em;
    display: grid;
    grid-template-columns: 50% 50%;
  }
  .swiper-nav{
    position: absolute;
    bottom: 1.875em;
    left: calc(50% + 1.875em);
    display: flex;
  }
  .swiper-button{
    position: static;
    margin: 0 1.625em 0 0;
    border: 1px solid $white;
    &:hover{
      border-color: $primary;
      background-color: $primary;
    }
  }
}


@media screen and (max-width: 992px){
  .main-catalog{
    .product-card{
      &__content{
        flex-direction: row;
      }
      &:nth-child(1){
        top: 2.5em;
        left: 1.125em;
      }
      &:nth-child(2){
        right: 1.125em;
        bottom: 2.19em;
      }
    }
  }

  .product-card {
    display: flex;
    flex-direction: column;
    &__img {
      padding-bottom: 90%;
    }
    &__content{
      flex-grow: 1;
      flex-direction: column;
      justify-content: space-between;
      gap: 1.5em 1em;
    }
  }

  .materials{
    &__col{
      padding: 1em;
    }
  }

  .materials-slider{
    .swiper-slide{
      min-height: 23em;
    }
    .swiper-nav{
      bottom: 1em;
      left: calc(50% + 1em);
    }
    .swiper-button{
      margin-right: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .materials{
    &__inner{
      padding: 1em 1em 4.5em;
      &:before{
        height: 29.5em;
        width: 36.5em;
        left: unset;
        right: 0;
      }
    }
    &__col{
      order: 2;
      padding: 2.25em 0 0;
      flex-grow: 1;
      display: flex;
      flex-direction: column;
    }
    &__col2{
      height: 16.875em;
    }
  }

  .materials-slider{
    .swiper-slide{
      display: flex;
      flex-direction: column;
    }
    .swiper-nav{
      bottom: 0;
      left: unset;
      right: 0;
    }
    .swiper-button{
      margin-right: 1em;
      width: 3em;
      height: 2.8125em;
      border: none;
      &:hover{
        background-color: transparent;
      }
    }
  }
}
