.header{
  position: relative;
  background-color: $black;
  color: $white;
  z-index: 20;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 3.125em;
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
}

.burger{
  display: none;
}

.nav__link,
.nav-item{
  margin-right: 3.125em;
  text-transform: uppercase;
  &:last-child{
    margin: 0;
  }
}

.catalog-subitem__show,
.header-catalog-item__show,
.nav__link,
.nav-item__show{
  span{
    font-size: .75em;
    letter-spacing: .1em;
    line-height: 167%;
  }
}

.nav{
  display: flex;
  align-items: center;
}

.header-contacts{
  display: flex;
  align-items: center;
  &__btn{
    color: $white;
    text-decoration: underline;
    margin-left: 2.5em;
    &:hover{
      color: $primary;
    }
  }
  &__phone{
    &:hover{
      color: $primary;
      path{
        stroke: $primary;
      }
    }
  }
}

.nav-item{
  &__show{
    display: flex;
    align-items: center;
    transition: all .3s;
    cursor: pointer;
    height: 3.125em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: 99%;
      height: 1em;
      width: 100%;
      left: 0;
      pointer-events: none;
    }
    svg{
      width: .8125em;
      margin-left: .375em;
    }
    &:hover{
      color: $primary;
      path{
        stroke: $primary;
      }
    }
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  @media screen and (min-width: 993px){
    &:hover{
      .nav-item__show{
        &:before{
          pointer-events: unset;
        }
      }
      .nav-item__hidden{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &__hidden{
      color: $black;
      position: absolute;
      top: 100%;
      left: 0;
      width: 100%;
      padding: 2.625em 3.75em;
      background-color: $white;
      z-index: 2;
      box-shadow: 0 30px 56px rgba(0, 0, 0, .1);
      display: block !important;
      transition: all .3s;
      opacity: 0;
      pointer-events: none;
      &:after{
        content: '';
        position: absolute;
        top: 100%;
        left: 0;
        width: 100%;
        height: $vh;
        background-color: $black;
        opacity: .6;
        pointer-events: none;
      }
    }
    .nav__link{
      margin: 0 0 3.81em;
      &:last-child{
        margin-bottom: 0;
      }
    }
    &._catalog{
      .nav-item__hidden{
        padding: 0 0 3.5em;
      }
    }
  }
}



@media screen and (max-width: 992px){
  .header{
    &._open{
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
  }

  .menu{
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    height: calc(100 * var(--vh) - 3.125em);
    background-color: $white;
    padding: 2em 1em;
    overflow-y: auto;
    color: $black;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
  }

  .burger{
    width: 1.6875em;
    position: relative;
    display: flex;
    img{
      transition: all .3s;
      width: 100%;
      @include centerAbsolute;
    }
    ._view2{
      width: 1.25em;
      opacity: 0;
    }
  }

  .header-contacts{
    &__btn{
      margin-left: 1.25em;
    }
  }

  .nav{
    flex-direction: column;
    align-items: flex-start;
    &__link{
      margin-bottom: 2em;
    }
  }

  .nav-item{
    margin-bottom: 2em;
    &__show{
      height: unset;
      path{
        stroke: $black;
      }
    }
    &__hidden{
      padding-left: 1em;
      display: none;
      margin-top: 1em;
    }
    .nav__link{
      margin-bottom: 1em;
      &:last-child{
        margin-bottom: 0;
      }
    }
    &._open{
      .nav-item__show svg{
        transform: rotate(-180deg);
      }
    }
  }
}


