.feedback{
  background-size: cover;
  background-position: center;
  padding: 14em 0;
  text-align: center;
  color: $white;
}

.feedback-form{
  width: 24.125em;
  max-width: 100%;
  margin: 1.31em auto 0;
  &__fields{
    @include grid100gap(.94em);
  }
  &__btn{
    width: 100%;
    margin-top: 2em;
  }
}

.m-section{
  &__top{
    @include center;
    padding: 1.5em;
    border-top: $border;
    border-bottom: $border;
  }
}

.suppliers{
  &__inner{
    margin-top: 2em;
    padding-right: .875em;
    padding-left: 3.75em;
    padding-bottom: 1.625em;
    display: grid;
    grid-template-columns: calc(100% - 51.5em) 41.5em;
    gap: 10em;
  }
  &__col{
    padding-top: 3.25em;
    display: flex;
    flex-direction: column;
  }
  &__img{
    width: 100%;
    display: block;
  }
  &__desc{
    margin-top: 1em;
  }
  &__nav{
    display: flex;
    margin-top: 1.875em;
  }
  &__nav2{
    display: flex;
    margin-top: 4.375em;
  }
}

.style{
  &__top{
    text-align: center;
    padding: 4.5em 0 4em;
    width: 52.5em;
    max-width: 100%;
    margin: 0 auto;
  }
  &__desc{
    margin-top: 1em;
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 1.875em;
  }
  &__grid{
    height: 44.5em;
    display: grid;
    grid-template-columns: calc(100% - 23.75em) 23.75em;
    grid-template-rows: 17.75em calc(100% - 17.75em);
    border-top: $border;
  }
  &__img{
    padding: .75em;
    &-inner{
      @include imgCover(0);
      height: 100%;
    }
  }
  &__img1{
    grid-column: 1/2;
    grid-row: 1/3;
    padding: .75em .75em .75em 1em;
    border-right: $border;
  }
}


@media screen and (max-width: 992px){
  .feedback{
    padding: 9.25em 0 12.81em;
  }

  .feedback-form{
    margin-top: 1em;
    &__btn{
      margin-top: 1.875em;
    }
  }

  .m-section{
    &__top{
      min-height: 5.625em;
    }
  }

  .suppliers{
    &__inner{
      margin-top: 1em;
      padding: 0 1em 2.5em;
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 2em 1em;
    }
    &__col{
      padding: 0;
    }
    &__nav2{
      display: none;
    }
  }

  .style{
    &__top{
      padding: 3.69em 0 2.19em;
    }
    &__grid{
      font-size: .625em;
    }
  }
}


@media screen and (max-width: 576px){
  .suppliers{
    &__inner{
      grid-template-columns: 100%;
    }
    &__col2{
      order: -1;
    }
  }

  .style{
    &__grid{
      font-size: 1em;
      grid-template-columns: 50% 50%;
      grid-template-rows: unset;
      border-top: none;
      height: unset;
    }
    &__img{
      padding: 0;
    }
    &__img1{
      height: 16.3125em;
      grid-column: 1/-1;
      grid-row: unset;
    }
    &__img2,
    &__img3{
      height: 13.75em;
    }
    &__img2{
      padding: .5em .25em .5em .5em;
    }
    &__img3{
      padding: .5em .5em .5em .25em;
    }
  }
}
