.text-center{
  text-align: center;
}

.caps-text{
  text-transform: uppercase;
}

.underline-text{
  text-decoration: underline;
}

.d1,
.h1,
.h2,
.h3{
  text-transform: uppercase;
  letter-spacing: -.02em;
  font-weight: 500;
  font-family: $font2;
}

.d1{
  text-transform: unset;
  font-size: 5.875em;
  line-height: 86%;
}

.h1{
  font-size: 3.5em;
  line-height: 114%;
}

.h2{
  font-size: 3em;
  line-height: 88%;
}

.h3{
  font-size: 2.25em;
  line-height: 117%;
}

.text20{
  font-size: 1.25em;
  line-height: 120%;
}

.text19{
  font-size: 1.1875em;
  line-height: 120%;
}

.text18{
  font-size: 1.125em;
  line-height: 120%;
}

.text17{
  font-size: 1.0625em;
  line-height: 120%;
}

.text16{
  font-size: 1em;
  line-height: 150%;
}

.text h5,
.text li,
.text p,
.text15{
  font-size: .9375em;
  line-height: 127%;
}

.text14{
  font-size: .875em;
  line-height: 120%;
}

.text13{
  font-size: .8125em;
  line-height: 120%;
}

.text12{
  font-size: .75em;
  line-height: 120%;
}

.text11{
  font-size: .6875em;
  line-height: 120%;
}

.text10{
  font-size: .625em;
  line-height: 120%;
}

.extra-bold-text{
  font-weight: 800;
}

.bold-text{
  font-weight: 700;
}

.semi-bold-text{
  font-weight: 600;
}

.medium-text{
  font-weight: 500;
}

.regular-text{
  font-weight: 400;
}

.light-text{
  font-weight: 300;
}

.lsp10{
  letter-spacing: .1em;
}

.dark-text{
  color: $dark;
}



@media screen and (max-width: 992px){
  .d1{
    font-size: 3em;
  }

  .h1{
    font-size: 2.375em;
    line-height: 121%;
  }

  .h2,
  .h3{
    font-size: 1.625em;
    line-height: 110%;
  }

  .text20-tablet{
    font-size: 1.25em;
  }

  .text19-tablet{
    font-size: 1.1875em;
  }

  .text18-tablet{
    font-size: 1.125em;
  }

  .text17-tablet{
    font-size: 1.0625em;
  }

  .text16-tablet{
    font-size: 1em;
  }

  .text15-tablet{
    font-size: .9375em;
  }

  .text14-tablet{
    font-size: .875em;
  }

  .text13-tablet{
    font-size: .8125em;
  }

  .text12-tablet{
    font-size: .75em;
  }

  .text11-tablet{
    font-size: .6875em;
  }

  .text10-tablet{
    font-size: .625em;
  }

}
