.pagination{
  padding: 3.125em 0;
  @include center;
  &__item{
    margin: 0 1em;
    font-weight: 500;
    &._active{
      pointer-events: none;
    }
    &:hover,
    &._active{
      color: $primary;
    }
  }
  .round-btn{
    margin: 0 1.5em;
    &:first-child{
      margin-left: 0;
    }
    &:last-child{
      margin-right: 0;
    }
  }
  ._disabled{
    border-color: #DFDEDB;
    pointer-events: none;
    path{
      fill: #DFDEDB;
    }
  }
}


@media screen and (max-width: 992px){
  .pagination{
    padding: 1.875em 0;
    &__item{
      font-size: .67em;
      margin: 0 .75em;
    }
    .round-btn{
      margin: 0 1em;
      width: 7.11463em;
      height: 7.11463em;
      svg{
        width: 3.75em;
      }
    }
  }
}


