.footer{
  background-color: $black;
  color: $white;
  &__logo{
    display: block;
    img{
      width: 100%;
      display: block;
    }
  }
}

.footer-top{
  border-top: $border;
  border-bottom: $border;
  .container{
    padding: 0 2.25em;
  }
  &__inner{
    display: grid;
    grid-template-columns: 10.75em 16em 14.75em 11.25em calc(100% - 52.75em);
  }
  &__col{
    padding: 1.4375em ;
    border-right: $border;
  }
  &__col1{
    padding-left: 0;
  }
  &__col5{
    border: none;
    padding-right: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.footer-nav{
  &__title{
    margin-top: 1.33em;
    &:first-child{
      margin-top: 0;
    }
  }
  &__links{
    @include grid100gap(.875em);
    margin-top: 1em;
    font-weight: 300;
    a{
      font-size: .75em;
      line-height: 117%;
      letter-spacing: .1em;
    }
  }
}

.footer-contacts{
  &__items{
    @include grid100gap(.875em);
    margin-top: 1em;
  }
  &__item{
    font-size: .75em;
    line-height: 117%;
    letter-spacing: .1em;
  }
}

.footer-middle{
  border-bottom: $border;
  position: relative;
  height: 19.5em;
  &__title{
    @include centerAbsolute;
    white-space: nowrap;
    font-family: $font2;
    font-size: 11.83694em;
    font-weight: 400;
    line-height: 86%;
    letter-spacing: -0.05em;
    text-transform: uppercase;
  }
}

.footer-bottom{
  padding: 1.5em 0;
  text-transform: uppercase;
  &__inner{
    display: flex;
    justify-content: center;
    gap: 0 .5em;
  }
}


@media screen and (max-width: 992px){
  .footer-top{
    .container{
      padding: 0;
    }
    &__inner{
      grid-template-columns: repeat(4,25%);
    }
    &__col{
      padding: 1em;
    }
    &__col4{
      border-right: none;
    }
    &__col5{
      border-top: $border;
      grid-column: 1/-1;
    }
  }

  .footer-middle{
    display: none;
  }

  .footer-bottom{
    padding: 1.25em 0;
  }
}


@media screen and (max-width: 576px){
  .footer-top{
    &__inner{
      grid-template-columns: repeat(2,50%);
    }
    &__col2{
      border-right: none;
    }
    &__col3,
    &__col4{
      border-top: $border;
    }
    &__col5{
      display: block;
    }
    .socials{
      margin-top: 1.2em;
    }
  }

  .footer-bottom{
    &__inner{
      display: block;
      text-align: center;
    }
    &__item{
      line-height: 157%;
    }
  }
}
