.catalog-item{
  padding-bottom: 3em;
  &__grid{
    margin-top: 1.69em;
    display: grid;
    grid-template-columns: calc(100% - 38.25em) 36.75em;
    gap: 1.5em;
  }
  &__info{
    align-items: flex-start;
  }
  &__info2{
    margin-top: 3.19em;
  }
  &__available{
    display: flex;
    align-items: center;
    padding: 0 1em;
    border: 1px solid $black;
    text-transform: uppercase;
    font-weight: 500;
    border-radius: 4.25em;
    height: 2.375em;
    span{
      font-size: 0.875em;
      letter-spacing: .1em;
    }
    img{
      width: 1.125em;
      margin-right: .5em;
    }
  }
  &__colors{
    display: flex;
  }
  &__color{
    margin-right: .94em;
    width: 3.125em;
    height: 3.125em;
    border-radius: 50%;
    &:last-child{
      margin: 0;
    }
    &._active{
      border: 1px solid $black;
    }
  }
  &__subtitle{
    font-size: 1em;
    line-height: 125%;
    letter-spacing: .1em;
    text-transform: uppercase;
    font-weight: 700;
  }
  .cartcalc{
    margin-left: 3em;
    flex-shrink: 0;
  }
}

.gallery{
  display: grid;
  grid-template-columns: 8.125em calc(100% - 8.125em - 1.5625em);
  gap: .625em 1.5625em;
  overflow: hidden;
  height: 35.3125em;
  &__small,
  &__big{
    height: 35.3125em;
  }
  .swiper{
    height: calc(100% + 1.5625em);
  }
  .swiper-slide{
    padding-bottom: 1.5625em;
  }
  &__small{
    .swiper-slide{
      height: 25%;
      &.swiper-slide-thumb-active .gallery__img{
        opacity: 1;
        &:before{
          opacity: 1;
        }
      }
    }
    .gallery__img{
      padding: 0;
      opacity: .5;
      &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border: 1px solid $black;
        opacity: 0;
      }
    }
  }
  &__img{
    @include imgCover(100%);
    min-height: 100%;
  }
}


@media screen and (max-width: 992px){
  .catalog-item{
    padding-bottom: 2.5em;
    &__grid{
      margin-top: 1.25em;
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
    }
    &__info{
      flex-direction: column;
    }
    &__info2{
      margin-top: 2em;
    }
    &__colors{
      margin-top: 1.25em;
    }
  }

  .gallery{
    grid-template-columns: 100%;
    height: unset;
    &__small,
    &__big{
      height: unset;
    }
    .swiper{
      height: auto;
      width: calc(100% + .625em);
    }
    .swiper-slide{
      padding: 0 .625em 0 0;
    }
    &__small{
      order: 2;
      .swiper-slide{
        height: auto;
        width: 25%;
      }
      .gallery__img{
        padding-bottom: 100%;
      }
    }
    &__img{
      padding-bottom: 81%;
    }
  }
}


@media screen and (max-width: 576px){
  .catalog-item{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
