.catalog-item{
  &__col2{
    display: flex;
    flex-direction: column;
  }
  &__bottom{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-top: auto;
    padding-top: 1em;
  }
  &__btn{
    width: 15.625em;
    height: 3.125em;
    span{
      font-size: .9375em;
    }
  }
  &__price{
    font-size: 3.31038em;
    line-height: 121%;
    font-weight: 700;
    letter-spacing: .1em;
  }
}

.set{
  margin-top: .25em;
  &__item{
    padding: 1.25em 0;
    min-height: 4em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: $border;
    &:last-child{
      border-bottom: none;
    }
    &-title{
      font-size: 0.875em;
      font-weight: 500;
      text-transform: uppercase;
      letter-spacing: .1em;
    }
  }
}

.select-option{
  &__label{
    display: flex;
    align-items: center;
    padding: .5em 1em;
    min-height: 2em;
    border-radius: .25em;
    font-size: .875em;
  }
  input:checked + .select-option__label{
    background-color: rgba($primary, .125);
  }
}

.m-select{
  position: relative;
  &__show{
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    &:after{
      content: '';
      width: .875em;
      height: .875em;
      margin-left: .375em;
      background-image: url(../img/select-arrow.svg);
      @include bgImgSize(100%);
    }
  }
  &__current{
    color: #6C6C6C;
    font-size: .75em;
    line-height: 117%;
    letter-spacing: .1em;
  }
  &__hidden{
    z-index: 3;
    position: absolute;
    top: 100%;
    right: 0;
    width: calc(100% + 2em);
    margin-top: .25em;
    background-color: $white;
    padding: .25em;
    border: $border;
    border-radius: .5em;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    overflow-y: auto;
    max-height: 30em;
  }
  &._open{
    .m-select__hidden{
      opacity: 1;
      pointer-events: unset;
    }
  }
}


@media screen and (max-width: 992px){
  .catalog-item{
    &__bottom{
      display: block;
    }
    &__btn{
      width: 100%;
      margin-top: 1.25em;
    }
  }

  .set{
    &__items{
      margin-top: 1em;
    }
    &__item{
      flex-direction: column;
      align-items: flex-start;
    }
    .m-select{
      margin-top: .625em;
    }
  }

  .m-select{
    &__hidden{
      left: 0;
      right: unset;
    }
  }
}


